@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Light.woff2") format("woff2"), url("../font/Rubik-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Regular.woff2") format("woff2"), url("../font/Rubik-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Medium.woff2") format("woff2"), url("../font/Rubik-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik";
  src: url("../font/Rubik-Bold.woff2") format("woff2"), url("../font/Rubik-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote {
  margin: 0;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

textarea {
  resize: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a,
input,
button {
  outline: 0;
  border: 0;
}

button {
  background-color: transparent;
  cursor: pointer;
}

/*---------------------------------*/

::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar {
  width: 2px;
  height: 2px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #ff6838;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Rubik", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: #31352b;
  background-color: #f9faff;
}

.logo {
  display: inline-block;
  border-radius: 5px;
  transition: opacity 0.3s, box-shadow 0.3s ease-in-out;
}

.logo:hover {
  opacity: 0.8;
}

.logo:focus {
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.logo:active {
  box-shadow: 0 0 0 2px transparent;
  opacity: 0.5;
}

.logo__img {
  width: 200px;
  height: 39px;
}

.link {
  border-radius: 5px;
  transition: color 0.3s, box-shadow 0.3s, text-shadow 0.3s ease-in-out;
}

.link:hover {
  color: #ff6838;
}

.link:focus {
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.link:active {
  box-shadow: 0 0 0 2px transparent;
  text-shadow: 2px 2px 5px rgba(49, 54, 43, 0.5019607843);
}

.link--active {
  color: #ff6838;
}

.title {
  font-weight: 500;
  line-height: 1.3;
  font-size: 36px;
  text-align: center;
  color: #363853;
}

.title--span {
  display: block;
  text-align: left;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #fff;
  border-radius: 5px;
  background-color: #ff6838;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.btn-bg:hover {
  color: #ff6838;
  background-color: rgba(255, 255, 255, 0.8901960784);
  box-shadow: 0 5px 12px rgba(255, 103, 56, 0.2), 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:focus {
  color: #ff6838;
  background-color: rgba(255, 122, 82, 0.1019607843);
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.btn-bg:active {
  color: #ff6838;
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px transparent;
}

.popular,
.restaurant,
.apps {
  padding: 120px 0;
}

/*---------------------------------*/

.lock {
  overflow: hidden;
  position: relative;
}

body.header-fixed header {
  position: fixed;
  top: 0;
  right: 0;
  padding-top: 0;
  width: 100%;
  z-index: 1;
}

.header {
  padding-top: 57px;
  width: 100%;
  position: fixed;
  background-color: #f9faff;
  transition: 0.2s all;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar__logo {
  margin-left: 5px;
}

.navbar__inner {
  display: flex;
  align-items: center;
}

.menu {
  width: 301px;
  display: flex;
  justify-content: space-between;
  margin-right: 50px;
}

.menu__link {
  font-size: 18px;
  line-height: 1.1666666667;
}

.user {
  display: flex;
}

.user__item:nth-child(1) {
  margin-right: 10px;
}

.user__item:nth-child(2) {
  position: relative;
}

.user__btn {
  width: 44px;
  height: 44px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.user__btn:hover {
  background-color: #ff6838;
}

.user__btn:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px inset rgba(255, 104, 56, 0.7019607843);
}

.user__btn:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.user__btn:hover .user__icon {
  stroke: #fff;
}

.user__btn:hover .user__count {
  color: #ff6838;
  background-color: #fff;
}

.user__btn:focus .user__icon {
  stroke: #363853;
}

.user__btn:focus .user__count {
  color: #fff;
  background-color: #ff6838;
}

.user__icon {
  stroke: #363853;
  transition: stroke 0.3s ease-in-out;
}

.user__count {
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  font-size: 10px;
  line-height: 1.2;
  color: #fff;
  background-color: #ff6838;
  top: 6px;
  right: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.3s, background-color 0.3s ease-in-out;
}

.burger {
  padding: 0;
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 5px;
  margin: 0 2px 0 10px;
  display: none;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.burger:hover {
  background-color: #ff6838;
}

.burger:hover .burger__line {
  background-color: #fff;
}

.burger:hover .burger__line::before,
.burger:hover .burger__line::after {
  background-color: #fff;
}

.burger:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px inset rgba(255, 104, 56, 0.7019607843);
}

.burger:focus .burger__line {
  background-color: #363853;
}

.burger:focus .burger__line::before,
.burger:focus .burger__line::after {
  background-color: #363853;
}

.burger:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.burger__line {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger__line::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  top: -10px;
  left: 0;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger__line::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  left: 0;
  bottom: -10px;
  background-color: #363853;
  transition: background-color 0.3s ease-in-out;
}

.burger-menu {
  display: block;
  position: absolute;
  width: 300px;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: #fff;
  transform: translateX(-1000%);
  transition: transform 0.3s ease-in-out;
}

.burger-menu__top {
  padding: 25px 26px 25px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eeeff3;
}

.burger-menu__close {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  transition: background-color 0.3s, box-shadow 0.3s ease-in-out;
}

.burger-menu__close:hover {
  background-color: #ff6838;
}

.burger-menu__close:hover .burger-menu__icon {
  fill: #fff;
}

.burger-menu__close:focus {
  background-color: rgba(255, 122, 80, 0.1019607843);
  box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7019607843);
}

.burger-menu__close:focus .burger-menu__icon {
  fill: #363853;
}

.burger-menu__close:active {
  background-color: #fff2ed;
  box-shadow: 0 0 0 2px transparent;
}

.burger-menu__icon {
  width: 18px;
  height: 18px;
  fill: #363853;
  transition: fill 0.3s ease-in-out;
}

.burger-menu .menu {
  display: block;
}

.burger-menu--active {
  transform: translateX(0%);
}

@media (max-width: 1200px) {
  .popular,
  .restaurant,
  .apps {
    padding: 90px 0;
  }
}

@media (max-width: 1000px) {
  body {
    font-size: calc(16px + 6.8 * ((100vw - 375px) / 1200));
  }

  .title {
    font-size: calc(24px + 20.4 * ((100vw - 375px) / 1200));
  }
}

@media (max-width: 767px) {
  .popular,
  .restaurant,
  .apps {
    padding: 60px 0;
  }
}

@media (max-width: 728px) {
  .menu {
    display: none;
  }

  .burger {
    display: initial;
  }
}

@media (max-width: 420px) {
  .header {
    padding-top: 14px;
  }

  .navbar__logo .logo__img {
    width: 116px;
    height: 23px;
  }
}

@media (max-width: 375px) {
  .popular,
  .restaurant,
  .apps {
    padding: 40px 0;
  }
}